<div :class="['side-bar__container', { stencil: isShowStencil }]"><section class="side-bar" v-if="!isProjectOpen"><router-link class="side-bar__logo" to="/"><base-icon class="side-bar__logo" name="logo"></base-icon></router-link><nav class="side-bar__nav nav"><ul class="nav__menu"><li v-for="item in navList" :key="item.id" :class="['nav__item', { active: item.path === this.$route.path }]"><router-link :to="item.path"><base-icon class="nav__icon" :name="item.name"></base-icon></router-link></li></ul></nav><router-link class="side-bar__logout" to="/login"><base-button class="side-bar__exit" customType="icon" name="exit" @handleClick="logout"></base-button></router-link></section><section class="side-bar project__toolbar" v-else><router-link class="side-bar__logo" to="/"><base-icon class="side-bar__logo" name="logo"></base-icon></router-link><transition name="fade" mode="out-in"><div class="toolbar" v-if="!isShowStencil"><base-button class="toolbar__btn toolbar__btn_svg" customType="icon" name="exportsvg"></base-button><base-button class="toolbar__btn toolbar__btn_png" customType="icon" name="exportpng"></base-button><base-button class="toolbar__btn toolbar__btn_print" customType="icon" name="print"></base-button></div><div id="stencil__container" v-else></div></transition><base-button class="project__wrench" customType="icon" name="wrench" @handleClick="changeIsShowStencil()"></base-button></section></div>