<div class="btn-component"><button class="btn btn_type_fill" v-if="customType === 'submit'" type="submit" :disabled="disabled"><slot></slot></button><button class="btn btn_type_fill" v-if="customType === 'fill'" @click="$emit('handleClick')" type="button" :disabled="disabled"><slot></slot></button><button class="btn btn_type_transparent" v-else-if="customType === 'transparent'" @click="$emit('handleClick')" type="button" :disabled="disabled"><slot></slot></button><button class="btn btn_type_icon" v-if="customType === 'icon'" @click="$emit('handleClick')" type="button" :disabled="disabled"><base-icon class="btn__icon" :name="name"></base-icon></button><button class="btn btn_type_file" v-if="customType === 'file'" @click="$emit('handleClick')" type="button" :disabled="disabled"><base-icon class="btn__icon" name="file"></base-icon><slot></slot></button></div>